nav .container{
    display: flex;
    justify-content: space-between;
    align-items: center;
}

#nav_p{
    font-family: 'Segoe UI';
    font-style: normal;
    font-weight: 700;
    font-size: 36px;
    line-height: 48px;
    color: #4579CB;
}

nav .container div{
    padding-top: 10px;
    padding-bottom: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 50px;
    font-family: 'Segoe UI';
    font-style: normal;
    font-weight: 700;
    font-size: 16px;
    line-height: 24px;
    color: #666666;
}

#btn_header{
    background: #4579CB;
    border-radius: 8px;
    width: 170px;
    height: 50px;
    font-family: 'Segoe UI';
    font-style: normal;
    font-weight: 700;
    font-size: 16px;
    line-height: 24px;
    text-align: center;
    color: #FFFFFF;    
    border: none;
}