footer{
    width: 100%;
    height: 350px;
    background: #4579CB;
}

footer .container{
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 100%;
}

#li_1{
    font-family: 'Segoe UI';
    font-style: normal;
    font-weight: 700;
    font-size: 20px;
    line-height: 28px;
    color: #FFFFFF;
}

#li_2{
    font-family: 'Segoe UI';
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 24px;
    display: flex;
    align-items: center;
    color: #FFFFFF;  
    display: flex;
    gap: 10px;
}

#flex{
    margin-top: 20px;
    display: flex;
    gap: 10px;
}