#btn_header{
    background: #4579CB;
    border-radius: 8px;
    width: 170px;
    height: 50px;
    font-family: 'Segoe UI';
    font-style: normal;
    font-weight: 700;
    font-size: 16px;
    line-height: 24px;
    text-align: center;
    color: #FFFFFF;    
    border: none;
}

/* Header */

header{
    margin-top: 150px;
}

#head_p1{
    font-family: 'Segoe UI';
    font-style: normal;
    font-weight: 700;
    font-size: 56px;
    line-height: 78px;
    text-align: center;
    letter-spacing: 0.02em;
    color: #474747;    
}

#head_p2{
    font-family: 'Segoe UI';
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    text-align: center;
    color: #666666;    
    margin-top: 30px;
}

header .container{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

header button{
    margin-top: 50px;
}

header span{
    color: #4579CB;
}

#head_p3{
    margin-top: 50px;
    font-family: 'Segoe UI';
    font-style: normal;
    font-weight: 700;
    font-size: 26px;
    line-height: 48px;
    text-align: center;
    color: #474747;    
}

#header_img{
    margin-top: 70px;
    width: 100%;
    display: flex;
    justify-content: space-between;
}
