@import url('https://fonts.googleapis.com/css2?family=DM+Sans:wght@500&family=Gothic+A1:wght@300&family=Jost:wght@300&family=Montserrat:wght@500&family=Mulish&family=Poppins:wght@500&family=Roboto:wght@300&family=Rubik:wght@500&family=Varela+Round&family=Work+Sans:wght@300;500&display=swap');

*{
    padding: 0px;
    margin: 0px;
    box-sizing: border-box;
    list-style: none;
}

.container{
    width: 85%;
    margin: auto;
}
/* Section 1 */

#sec_1 .container{
    width: 65%;
    margin-top: 100px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: column;
}

#sec_1 .container div div{
    display: flex;
    justify-content:baseline;
    align-items:baseline;
    flex-direction: column;
    gap: 20px;
}

#sec_1 .container div{
    display: flex;
    gap: 200px;
    padding-top: 100px;
    padding-bottom: 100px;
}

#sec_1 .container{
    display: block;
}

#sec1_p{
    font-family: 'Segoe UI';
    font-style: normal;
    font-weight: 700;
    font-size: 26px;
    line-height: 48px;
    color: #474747;
}

#sec2_p{
    font-family: 'Segoe UI';
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    color: #666666;
}

#sec_2{
    padding-bottom: 50px;
}

/* Section 2 */

#sec_2_p_1{
    font-family: 'Segoe UI';
    font-style: normal;
    font-weight: 700;
    font-size: 26px;
    line-height: 48px;
    text-align: center;
    color: #474747;
}

#sec_2_p_2{
    font-family: 'Segoe UI';
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    text-align: center;
    color: #666666;    
}

#sec_2_grid{
    display: grid;
    grid-template-columns: repeat(4,1fr);
    gap: 30px;
    margin-top: 100px;
}

#sec_2 .container{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

#sec_2_grid div{
    background: #F6F7FD;
    border-radius: 8px;
    width: 270px;
    height: 267px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

#sec{
    margin-top: 100px;
    width: 70%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-top: 100px;
    padding-bottom: 100px;
}

#btns{
    display: flex;
    gap: 30px;
}

#btn{
    background: #E6E6E6;
    border-radius: 4px;
    width: 70px;
    height: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
}

#btn_1{
    background: #4579CB;
    border-radius: 4px;
    width: 70px;
    height: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
}

#btn_1 img{
    transform: rotate(-180deg);
}

#sec_2_p_3{
    font-family: 'Segoe UI';
    font-style: normal;
    font-weight: 700;
    font-size: 16px;
    line-height: 24px;
    color: #474747;
}

#sec_2_p_4{
    font-family: 'Segoe UI';
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 24px;
    color: #474747;
}

#sec_2_p_5{
    font-family: 'Segoe UI';
    font-style: normal;
    font-weight: 700;
    font-size: 20px;
    line-height: 28px;
    color: #474747;
}

#sec div{
    padding-top: 15px;
    padding-bottom: 15px;
}

#imgs img{
    border-radius: 15px;
}

#imgs{
    display: flex;
    justify-content: center;
    gap: 30px;
    
    padding-top: 50px;
    padding-bottom: 50px;
}

#sec_2_grid_2{
    display: grid;
    grid-template-columns: repeat(2,1fr);
    margin-top: 100px;
    gap: 30px;
    padding-bottom: 50px;
}

#sec_2_grid_2 div{
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 20px;
    border: 1px solid #CACACA;
    border-radius: 10px;
    width: 570px;
    height: 70px;  
    
    font-family: 'Segoe UI';
    font-style: normal;
    font-weight: 700;
    font-size: 16px;
    line-height: 24px;
    color: #8C8C8C;
}

#overflow{
    display: flex;
    justify-content: space-between;
    width: 95%;
    margin: auto;
    padding-top: 100px;
    padding-bottom: 100px;
}

#overflow div{
    padding: 20px;
    background: #FFFFFF;
    border-radius: 16px;
    width: 200px;
    height: 239px;
    box-shadow: 0px 0px 8px 0px rgba(34, 60, 80, 0.2);
}

#ower_p_1{
    font-family: 'Segoe UI';
    font-style: normal;
    font-weight: 700;
    font-size: 20px;
    line-height: 28px;
    text-align: center;
    color: #4579CB;
}
